.header {
	background-color: var(--light-color);

	&__top {
		display: flex;
		align-items: flex-end;
		justify-content: flex-end;
		margin-left: 367px;
		border: 2px solid #2186FA;
		border-radius: 0px 0px 100px 100px;
		max-width: 800px;
		height: 30px;
		background-color: var(--blue-color);
		overflow: hidden;

		@include tablet {
			margin: 0;
			max-width: 100%;
		}

		@include mobile {
			justify-content: center;
		}
	}

	&__basket {
		display: flex;
		flex-shrink: 0;
		padding-left: 80px;
		padding-right: 30px;
		padding-bottom: 3px;
	}

	&__link {
		position: relative;
		padding-left: 30px;

		svg {
			position: absolute;
			left: 0;
			bottom: 0;
			width: 19px;
			height: 19px;
			fill: var(--light-color);
		}

		&:focus {
			outline: 2px solid var(--light-color);
			outline-offset: 2px;
		}
	}

	&__value {
		font-weight: 600;
		font-size: 14px;
		line-height: 16px;
		color: var(--light-color);
		transition: color 0.3s ease-in-out;

		&:hover {
			color: var(--dark-color);
		}
	}

	&__center {
		margin-bottom: 10px;
		background-color: var(--light-color);
	}

	&__bottom {
		background-color: var(--blue-color);
	}

	&__nav {
		margin-left: 40px;

		@include small-desktop {
			margin-left: 0;
		}
	}

	.burger {
		@include burger;
	}
}

.header-logo {
	&:focus {
		outline: 2px solid var(--blue-color);
		outline-offset: 2px;
	}

	@include tablet {
		display: none;
	}
}

.specver-toggle {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	border-radius: 0px 0px 100px 100px;
	width: 95%;
	height: 100%;
	background-color: var(--light-color);
	font-weight: 600;
	font-size: 14px;
	line-height: 114%;
	color: var(--blue-color);
	text-transform: uppercase;

	&__icon {
		margin-right: 10px;
		width: 23px;
		height: 17px;
		background-image: url('/img/header/optic.png');
		background-position: left;
		background-size: cover;
		background-repeat: no-repeat;
	}

	@include mobile {
		display: none;
	}
}

.bottom {
	&__container {
		display: flex;
		justify-content: space-between;

		@include tablet {
			align-items: center;
			justify-content: flex-end;
		}
	}
}

.header-contacts {
	display: flex;
	justify-content: space-between;
	align-items: center;

	&__link {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding-left: 45px;
		background-position: left center;
		background-size: 30px 32px;
		background-repeat: no-repeat;

		&--map {
			background-image: url('/img/header/map.png');
		}

		&--calendar {
			background-image: url('/img/header/calendar.png');
		}

		&--call {
			background-image: url('/img/header/phone.png');
		}

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}

		@include tablet {
			display: flex;
			align-items: flex-start;

			&:not(:last-child) {
				margin-bottom: 30px;
				padding: 0;
			}
		}
	}

	&__social {
		@include tablet {
			max-width: 250px;
		}
	}

	@include tablet {
		display: none;
	}

	&--nav {
		display: none;

		@include tablet {
			display: block;

		}
	}
}

.contacts-link {

	&__caption {
		font-weight: 400;
		font-size: 14px;
		line-height: 114%;
		color: var(--dark-color);

		@include tablet {
			font-weight: 700;
			font-size: 16px;
		}
	}

	&__value {
		font-weight: 600;
		font-size: 17px;
		line-height: 117%;
		color: var(--dark-color);
	}
}

.header-social {
	background: #00E676;
	border-radius: 20px;
	transition: background-color 0.3s ease-in-out;

	&:hover {
		background-color: var(--blue-color);
	}

	&__link {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 8px 15px;

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}

		@include tablet {
			justify-content: center;
		}
	}

	&__icon {
		margin-right: 10px;
		width: 25px;
		height: 27px;
		object-fit: cover;
		fill: var(--light-color);
	}

	&__caption {
		font-weight: 600;
		font-size: 14px;
		line-height: 114%;
		color: var(--light-color);

		@include mobile {
			font-size: 13px;
		}
	}
}

.register {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 15px 25px;
	border-radius: 20px 0px;
	border: 3px solid #FB4721;
	width: 260px;
	background-color: #F06150;
	transition: background-color 0.3s ease-in-out, border 0.3s ease-in-out;

	&:hover {
		border: 3px solid #00E676;
		background-color: #00E676;
	}

	&__link {
		font-weight: 800;
		font-size: 17px;
		line-height: 117%;
		text-align: center;
		color: var(--light-color);
		text-transform: uppercase;

		&:focus {
			outline: 2px solid var(--light-color);
			outline-offset: 2px;
		}

		@include mobile {
			font-size: 14px;
		}
	}

	@include small-desktop {
		padding: 15px 0;
		margin-left: 20px;
	}

	@include tablet {
		margin-left: 0;
		margin-right: 50px;
	}
}