.graph-modal {
	&__container {
		max-width: 600px;
	}

	.form-contacts {
		display: grid;
		grid-template-columns: repeat(6, 1fr);
		column-gap: 30px;
	}

	.form-contacts__left {
		grid-column: 3 span;
		width: 100%;
	}

	.form-contacts__right {
		grid-column: 3 span;
		width: 100%;
	}

	.form-contacts__bottom {
		grid-column: 6 span;
		align-items: center;
		width: 100%;
	}
}