.benefits {
	padding-top: 80px;
	padding-bottom: 80px;
	background-color: var(--blue-color);

	&__title {
		margin: 0;
		margin-bottom: 60px;
		width: 100%;
		color: var(--light-color);
	}

	&__inner {
		display: grid;
		grid-template-columns: repeat(12, 1fr);
		column-gap: 40px;

		@include small-tablet {
			grid-template-columns: repeat(4, 1fr);
		}
	}

	&__left {
		grid-column: 7 span;
		display: flex;

		@include tablet {
			flex-wrap: wrap;
		}

		@include small-tablet {
			grid-column: span 4;
			margin-bottom: 50px;
		}
	}

	&__picture {
		flex-shrink: 0;
		margin-right: 40px;
		width: 300px;
		height: 300px;
		border: 1px solid #FFFFFF;
		filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
		border-radius: 25px;
		overflow: hidden;

		@include tablet {
			margin-right: 0;
			margin-bottom: 30px;
		}
	}

	&__image {
		display: block;
	}

	&__right {
		grid-column: 5 span;

		@include small-tablet {
			grid-column: span 4;
		}
	}

	&__list {
		display: flex;
		flex-direction: column;
	}

	&__item {
		&:not(:last-child) {
			margin-bottom: 30px;
		}
	}

	&__icon--star {
		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 35px;
			height: 35px;
			background-image: url('/img/benefits/star.svg');
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			fill: var(--light-color);
		}
	}

	&__icon--people {
		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 30px;
			height: 30px;
			background-image: url('/img/benefits/people.svg');
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			fill: var(--light-color);
		}
	}

	&__icon--heart {
		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 30px;
			height: 30px;
			background-image: url('/img/benefits/heart.svg');
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			fill: var(--light-color);
		}
	}

	&__content {
		position: relative;
		padding-left: 50px;
	}

	&__subtitle {
		margin: 0;
		margin-bottom: 15px;
		font-weight: 700;
		font-size: 20px;
		line-height: 115%;
		color: var(--light-color);
	}

	&__descr {
		margin: 0;
		font-weight: 500;
		font-size: 18px;
		line-height: 116%;
		color: #E0E0E0;

	}
}

.article {
	position: relative;

	&--quote {

		&::before {
			content: '';
			position: absolute;
			left: -70px;
			top: -27px;
			width: 60px;
			height: 50px;
			fill: var(--light-color);
			background-image: url('/img/benefits/quote.png');
			background-size: contain;
			background-position: center;
			background-repeat: no-repeat;

			@include tablet {
				left: 270px;
				top: -357px;
			}

			@include mobile {
				left: 252px;
				top: -351px;
				width: 50px;
				height: 40px;
			}
		}
	}

	&__content {
		margin-bottom: 35px;
	}

	&__descr {
		margin: 0;
		font-weight: 500;
		font-size: 16px;
		line-height: 118%;
		color: var(--light-color);

		&:not(:last-child) {
			margin-bottom: 20px;
		}
	}

	&__author {
		position: relative;
		padding-top: 20px;

		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 40px;
			height: 3px;
			background-color: #C1F100;
		}
	}

	&__name {
		margin: 0;
		font-weight: 900;
		font-size: 18px;
		line-height: 116%;
		color: var(--light-color);
	}

	&__text {
		margin: 0;
		font-weight: 500;
		font-size: 18px;
		line-height: 116%;
		color: #E0E0E0;

		@include mobile {
			font-size: 16px;
		}
	}
}