.footer {
	padding-top: 40px;
	padding-bottom: 100px;
	background-color: var(--solitude-color);

	&__container {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;

		@include tablet {
			display: block;
		}
	}


	&__nav {
		@include mobile {
			margin-bottom: 50px;
		}
	}

	&__left {
		flex: 1 0 315px;
		margin-right: 30px;

		@include mobile {
			margin-right: 0;
		}
	}

	&__icon {
		display: block;
		margin-bottom: 20px;
	}

	&__descr {
		display: inline-block;
		margin: 0;
		margin-bottom: 115px;
		color: var(--dark-color);

		p {
			margin: 0;
			margin-bottom: 15px;
			font-weight: 500;
			font-size: 19px;
			line-height: 115%;
			text-decoration-line: underline;
		}

		span {
			font-size: 17px;
			line-height: 20px;
		}

		@include tablet {
			margin-bottom: 20px;
		}
	}

	&__address {
		p {
			margin: 0;
			font-style: normal;
			font-size: 17px;
			line-height: 20px;
		}

		@include tablet {
			margin-bottom: 40px;
		}
	}

	&__contacts {
		flex: 1 0 200px;

		@include mobile {
			flex: 1 1 100%;
		}
	}
}

.footer-nav {
	display: flex;
	flex-wrap: wrap;

	&__col {
		flex: 0 1 50%;
		padding-left: 30px;
		padding-right: 30px;

		&:not(:last-child) {
			margin-bottom: 50px;
		}

		@include mobile {
			flex: 1 1 100%;
			padding-left: 0;
			padding-right: 0;
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 15px;
		font-weight: 500;
		font-size: 22px;
		line-height: 26px;
		text-decoration-line: underline;
		color: var(--dark-color);
	}

	&__item {
		&:not(:last-child) {
			margin-bottom: 5px;
		}
	}

	&__link {
		font-size: 17px;
		line-height: 20px;
		color: var(--dark-color);

		&:hover {
			text-decoration: underline;
		}
	}
}

.footer-contacts {
	display: flex;
	flex-direction: column;

	&__list {
		margin-bottom: 35px;
	}

	&__item {
		display: flex;
		align-items: center;
		padding-left: 50px;
		height: 27px;

		&:not(:last-child) {
			margin-bottom: 25px;
		}

		&--call {
			background-image: url('/img/footer/call.png');
			background-position: left;
			background-size: contain;
			background-repeat: no-repeat;
		}

		&--mes {
			background-image: url('/img/footer/mes.png');
			background-position: left;
			background-size: contain;
			background-repeat: no-repeat;
		}

		&--mail {
			background-image: url('/img/footer/mail.png');
			background-position: left;
			background-size: contain;
			background-repeat: no-repeat;
		}
	}

	&__value {
		margin: 0;
		margin-bottom: 5px;
		font-weight: 600;
		font-size: 17px;
		color: var(--dark-color);
	}
}

.footer-direction {
	margin-bottom: 30px;

	&__col {
		display: flex;
		align-items: center;
		padding-left: 35px;
		height: 26px;

		&--sign {
			background-image: url('/img/footer/sign.png');
			background-position: left;
			background-size: contain;
			background-repeat: no-repeat;
		}

		&:not(:last-child) {
			margin-bottom: 15px;
		}
	}

	&__descr {
		margin: 0;
		font-size: 12px;
		line-height: 116%;
		text-decoration-line: underline;
		color: var(--blue-color);
		transition: color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}

		&:hover {
			color: var(--dark-color);
		}

		@include mobile {
			font-size: 14px;
		}
	}
}

.footer-form {
	&__input {
		margin-bottom: 10px;
		padding: 12px 18px;
		border-radius: 9px;
		border: 1px solid rgba(59, 59, 59, 0.31);

		&::placeholder {
			font-size: 13px;
		}

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}

		@include mobile {
			margin-bottom: 20px;
			width: 100%;
		}
	}

	&__btn {
		display: flex;
		align-items: center;
		padding: 10px 40px;
		height: 40px;
		text-align: center;
		font-size: 13px;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}

		&:hover {
			background-color: var(--light-color);
			color: var(--blue-color);
		}

		@include mobile {
			font-size: 16px;
		}
	}
}