.feedback {
	padding-bottom: 80px;
	background-color: var(--light-color);

	&__container {}

	&__box {
		margin-bottom: 35px;
		background-image: url('/img/pencil.png');
		background-position: left;
		background-size: 36px 36px;
		background-repeat: no-repeat;
	}

	&__title {
		margin: 0;
		padding-left: 52px;
	}

	&__form {
		margin-bottom: 40px;
	}

}

.form-feedback {
	display: flex;
	flex-wrap: wrap;

	&__field {
		flex: 0 1 40.5%;

		&:not(:last-child) {
			margin-right: 50px;
			margin-bottom: 40px;

			@include tablet {
				margin-right: 0;
			}
		}

		&--big {
			flex: 0 1 85.5%;
		}

		.form-field {
			&__caption {
				padding-bottom: 8px;
				font-size: 22px;
				line-height: 122%;

				@include mobile {
					font-size: 20px;
				}
			}

			&__input {
				padding: 25px;

				&::placeholder {
					font-size: 22px;
					line-height: 26px;
					color: #B0B9C2;

					@include tablet {
						font-size: 18px;
					}
				}

				@include tablet {
					padding: 15px;
					font-size: 18px;
				}
			}

			&__input--big {
				height: 235px;

				@include tablet {
					height: 150px;
				}
			}
		}
	}

	&__bottom {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		flex-wrap: wrap;
	}

	&__btn {
		margin-right: 35px;
		padding: 22px 60px;
		border-radius: 40px;
		font-weight: 500;
		font-size: 24px;
		color: var(--light-color);
		background-color: var(--blue-color);
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}

		&:hover {
			background-color: var(--light-color);
			color: var(--blue-color);
		}

		@include mobile {
			padding: 20px 40px;
			margin-bottom: 20px;
			font-size: 22px;
		}
	}

	&__checkbox {
		.custom-checkbox__text {
			font-size: 14px;
			line-height: 128%;

			a {
				text-decoration-line: underline;
				color: var(--blue-color);

				&:focus {
					outline: 2px solid var(--blue-color);
					outline-offset: 2px;
				}
			}
		}

		.custom-checkbox__consent::before {
			top: -3px;
		}

		.custom-checkbox__consent::after {
			top: -3px;
		}
	}

	@include tablet {
		display: block;
	}
}