.nav {
	width: 810px;

	&__list {
		display: flex;
		justify-content: space-between;
		height: 100%;

		@include tablet {
			display: block;
			margin-bottom: 30px;
			height: auto;
		}
	}

	&__item {
		display: flex;
		align-items: center;
		justify-content: center;
		align-self: center;
		transition: background-color 0.3s ease-in-out;

		&:hover {
			background-color: var(--light-color);
			color: var(--blue-color);
		}

		@include tablet {
			display: block;
			border-radius: 5px;

			&:not(:last-child) {
				margin-bottom: 5px;
			}
		}
	}


	.nav__item:hover {
		@include tablet {
			color: var(--dark-color);
			background: none;
		}
	}

	&__link {
		padding: 17px 14px;
		font-weight: 600;
		font-size: 17px;
		line-height: 117%;
		color: var(--light-color);
		text-transform: uppercase;
		transition: color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--light-color);
			outline-offset: 2px;
		}

		&:hover {
			color: var(--blue-color);
		}

		@include small-desktop {
			padding: 17px 5px;
		}

		@include tablet {
			display: inline-block;
			padding: 10px 0;
			font-weight: 700;
			font-size: 24px;
		}
	}

	.nav__link:hover {
		@include tablet {
			color: var(--light-color);
			text-decoration: underline;
		}
	}

	@include tablet {
		position: fixed;
		left: 0;
		top: 0;
		z-index: 1000;
		display: block;
		overflow-y: auto;
		margin-left: 0;
		padding: 30px;
		width: 80%;
		height: 100vh;
		background-color: var(--blue-color);
		transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
		opacity: 0;
		visibility: hidden;

		&--active {
			opacity: 1;
			visibility: visible;
		}
	}

}