.tooltips {
	padding-top: 80px;
	padding-bottom: 80px;
	background-color: var(--light-color);

	&__title {
		margin: 0;
		margin-bottom: 65px;
		width: 100%;
	}

	&__box {
		display: flex;
		align-items: center;
		justify-content: center;
	}

	@include small-tablet {
		display: none;
	}

}

.box-tooltip {
	&__left {
		position: relative;
	}
}

.tooltip {
	position: absolute;
	left: 80px;
	top: 12px;


	&__btn {
		position: relative;
		width: 30px;
		height: 30px;

		&:focus {
			+.tooltip__txt {
				opacity: 1;
				visibility: visible;
			}
		}

		@include hover {
			+.tooltip__txt {
				opacity: 1;
				visibility: visible;
			}
		}
	}

	&__icon {
		position: absolute;
		left: 0px;
		top: 0;
		width: 30px;
		height: 30px;
		background-image: url('/img/tooltips/info-icon.png');
		background-position: center;
		background-size: contain;
		background-repeat: no-repeat;

		&--val1 {
			left: 0;
			top: 0;
		}

		&--val2 {
			left: -71px;
			top: 186px;
		}

		&--val3 {
			left: -29px;
			top: 288px;
		}

		&--val4 {
			left: -26px;
			top: 442px;
		}

		&--val5 {
			left: 135px;
			top: 68px;
		}

		&--val6 {
			left: 133px;
			top: 371px;
		}

		&--val7 {
			left: 461px;
			top: 55px;
		}

		&--val8-light {
			left: 384px;
			top: 132px;
			background-image: url('/img/tooltips/info-icon-light.png');
		}

		&--val9-light {
			left: 416px;
			top: 219px;
			background-image: url('/img/tooltips/info-icon-light.png');
		}

	}

	&__txt {
		border-radius: 20px;
		padding: 20px;
		width: 300px;
		background: #E5F1FF;
		opacity: 0;
		visibility: hidden;
		transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
	}

	&__subtitle {
		margin: 0;
		margin-bottom: 20px;
		font-weight: 500;
		font-size: 22px;
		line-height: 118%;
		color: var(--dark-color);
	}

	&__list {}

	&__item {
		background-image: url('/img/svg/rhomb.svg');
		background-position: left 4px;
		background-size: 10px 10px;
		background-repeat: no-repeat;

		&:not(:last-child) {
			margin-bottom: 8px;
		}
	}

	&__descr {
		margin: 0;
		padding-left: 15px;
		font-weight: 300;
		font-size: 16px;
		line-height: 104%;
		color: var(--dark-color);
	}
}