.assistance {
	padding-top: 80px;
	padding-bottom: 80px;

	&__title {
		margin: 0;
		margin-bottom: 65px;
	}

	&__column {
		display: grid;
		grid-template-columns: repeat(12, 1fr);

		@include small-tablet {
			grid-template-columns: repeat(4, 1fr);
		}
	}

	&__sidebar {
		grid-column: 6 span;
		margin-right: 30px;

		@include small-tablet {
			grid-column: span 4;
		}
	}

	&__list {
		grid-column: 6 span;
	}
}

.sidebar-assistance {
	max-width: 500px;

	&__wrapper {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 15px;
		z-index: 15;
		width: 100%;
		height: 100px;
		background-color: var(--blue-color);
		background: linear-gradient(180deg, #2186FA 0%, rgba(33, 134, 250, 0.7) 100%);
		border: 5px solid #2186FA;
		border-radius: 25px;
	}

	&__subtitle {
		margin: 0;
		width: 395px;
		font-weight: 700;
		font-size: 26px;
		line-height: 115%;
		color: var(--light-color);

		@include tablet {
			width: 100%;
		}

		@include mobile {
			font-size: 20px;
		}
	}

	&__inner {
		position: relative;
		top: -20px;
		padding: 42px 22px 33px 41px;
		border-radius: 25px;
		border: 5px solid #E5F1FF;
		border-top: none;

		@include small-tablet {
			padding: 20px;
			padding-top: 40px;
		}
	}

	&__list {
		display: flex;
		flex-direction: column;
		justify-content: center;
		margin-bottom: 25px;
	}

	&__item {
		display: flex;
		align-items: center;

		&:not(:last-child) {
			margin-bottom: 15px;
		}
	}

	&__icon {
		display: inline-block;
		flex-shrink: 0;
		width: 15px;
		height: 15px;
		object-fit: cover;
		fill: var(--blue-color);
	}

	&__descr {
		margin: 0;
		padding-left: 12px;
		font-weight: 400;
		font-size: 18px;
		line-height: 116%;
		color: var(--dark-color);

		@include tablet {
			font-size: 16px;
		}
	}

	&__slogan {
		margin: 0;
		padding-left: 27px;
		font-weight: 700;
		font-size: 18px;
		line-height: 116%;
		color: #B6E300;

		@include mobile {
			padding-left: 0;
		}
	}

	@include small-tablet {
		max-width: 100%;
	}
}

.list-assistance {
	counter-reset: section;

	&__item {
		display: flex;
		align-items: center;

		&::before {
			content: counter(section);
			counter-increment: section;
			font-weight: 900;
			font-size: 67px;
			background: linear-gradient(180deg, #2186FA 0%, rgba(226, 240, 255, 0) 100%);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;

			@include mobile {
				font-size: 62px;
			}
		}

		@include mobile {
			&:not(:last-child) {
				margin-bottom: 10px;
			}
		}
	}

	&__descr {
		margin: 0;
		margin-left: 22px;
		font-weight: 500;
		font-size: 22px;
		line-height: 118%;
		color: var(--dark-color);

		@include tablet {
			font-size: 20px;
		}
	}
}