.main-page {
	position: relative;
	padding-top: 60px;
	height: 430px;
	background-image: url('/img/main-page/main-page-bg.jpg');
	background-position: right center;
	background-size: cover;
	background-repeat: no-repeat;

	&::before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(90deg, #FFFFFF 30.5%, rgba(255, 255, 255, 0) 50%);

		@include tablet {
			background: linear-gradient(90deg, #FFFFFF 4.5%, rgba(255, 255, 255, 0) 135%);
		}
	}

	&::after {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(270deg, #FFFFFF 1.5%, rgba(255, 255, 255, 0) 4%);
	}

	&__title {
		position: relative;
		z-index: 20;
		margin: 0;
		margin-bottom: 70px;
		width: 680px;
		font-weight: 700;
		font-size: 46px;
		font-style: normal;
		line-height: 117%;
		color: var(--dark-color);

		span {
			display: block;
			padding-top: 18px;
			font-weight: 400;
			font-size: 20px;
			line-height: 115%;
		}

		@include small-tablet {
			font-size: 40px;
		}

		@include mobile {
			width: 100%;
			font-size: 30px;
		}
	}

	&__content {
		position: relative;
		z-index: 20;
		display: flex;
		justify-content: space-between;

		@include tablet {
			flex-wrap: wrap;
		}
	}

	&__slogan {
		display: flex;
		align-items: center;
		max-width: 470px;

		@include tablet {
			margin-right: 115px;
			margin-bottom: 30px;
		}

		@include mobile {
			margin-right: 0;
		}
	}

	&__icon {
		display: inline-block;
		flex-shrink: 0;
		margin-right: 15px;
		width: 40px;
		height: 41px;
		background-image: url('/img/main-page/doctor.png');
		background-position: left;
		background-size: contain;
		background-repeat: no-repeat;

		@include mobile {
			display: none;
		}
	}

	&__text {
		margin: 0;
		font-weight: 500;
		font-size: 20px;
		line-height: 115%;
		color: var(--dark-color);

		@include mobile {
			font-weight: 600;
		}
	}

	@include tablet {
		height: 100vh;
	}
}

.video {
	display: flex;
	align-items: center;
	margin-right: 80px;
	border-radius: 100px;
	width: 330px;
	background-color: var(--light-color);

	&__title {
		margin: 0;
		padding: 20px 10px 20px 20px;
		font-weight: 400;
		font-size: 16px;
		line-height: 118%;
		color: #3B3B3B;

		span {
			display: block;
			padding-top: 5px;
			text-align: right;
			color: #2186FA;
		}
	}

	&__play {
		padding-right: 10px;
	}

	&__icon {
		width: 60px;
		height: 60px;
		fill: #2186FA;
	}

	@include tablet {
		margin-bottom: 80px;
	}

	@include small-tablet {
		display: none;
	}
}