.about {
	background-color: var(--solitude-color);

	&__container {}

	&__list {
		display: grid;
		grid-template-columns: repeat(12, 1fr);
		column-gap: 60px;

		@include small-tablet {
			gap: 30px;
		}

		@include mobile {
			grid-template-columns: repeat(4, 1fr);
			row-gap: 30px;
		}
	}

	&__item {
		position: relative;
		grid-column: 4 span;
		padding-top: 95px;
		padding-bottom: 65px;
	}


	&__descr {
		margin: 0;
		font-weight: 400;
		font-size: 16px;
		line-height: 118%;
		color: var(--dark-color);

		@include mobile {
			font-size: 18px;
		}
	}

	&__item--hospital {
		&::before {
			content: '';
			position: absolute;
			left: 50%;
			top: -36px;
			border-radius: 100%;
			border: 10px solid #E5F1FF;
			width: 110px;
			height: 110px;
			background-color: var(--light-color);
			background-image: url('/img/about/hospital.png');
			background-position: center;
			background-size: 50px 47px;
			background-repeat: no-repeat;
			transform: translateX(-50%);
		}
	}

	&__item--care {
		&::before {
			content: '';
			position: absolute;
			left: 50%;
			top: -36px;
			border-radius: 100%;
			border: 10px solid #E5F1FF;
			width: 110px;
			height: 110px;
			background-color: var(--light-color);
			background-image: url('/img/about/care.png');
			background-position: center;
			background-size: 50px 47px;
			background-repeat: no-repeat;
			transform: translateX(-50%);
		}
	}

	&__item--card {
		&::before {
			content: '';
			position: absolute;
			left: 50%;
			top: -36px;
			border-radius: 100%;
			border: 10px solid #E5F1FF;
			width: 110px;
			height: 110px;
			background-color: var(--light-color);
			background-image: url('/img/about/card.png');
			background-position: center;
			background-size: 42px 50px;
			background-repeat: no-repeat;
			transform: translateX(-50%);
		}
	}
}