.contacts {
	padding-top: 80px;
	padding-bottom: 80px;
	background-color: var(--solitude-color);

	&__title {
		margin: 0;
		margin-bottom: 50px;
	}

	&__content {
		position: relative;
		display: flex;
		justify-content: space-between;

		&::before {
			position: absolute;
			content: '';
			right: 7px;
			top: -140px;
			width: 60px;
			height: 60px;
			z-index: 10;
			background-image: url('/img/benefits/quote.png');
			background-position: center;
			background-size: contain;
			background-repeat: no-repeat;

			@include tablet {
				display: none;
			}
		}

		@include tablet {
			justify-content: center;
		}

		@include small-tablet {
			display: block;
		}
	}

	&__image {
		position: relative;
		border-radius: 27px;
		transform: translateY(-105px);
		overflow: hidden;

		@include tablet {
			display: none;
		}
	}
}

.form-contacts {
	display: flex;
	flex-wrap: wrap;
	align-content: flex-start;
	max-width: 600px;

	&__left {
		margin-right: 50px;
		width: 229px;

		@include small-tablet {
			margin-right: 0;
			width: 100%;
		}
	}

	&__field {
		&:not(:last-child) {
			margin-bottom: 30px;
		}
	}

	&__inner {
		display: flex;
		align-items: center;
		justify-content: space-between;

		@include small-tablet {
			justify-content: flex-start;
		}
	}

	&__date {
		@include small-tablet {
			margin-right: 30px;
		}
	}

	&__right {
		width: 232px;

		@include tablet {
			flex: 0 1 50%;
		}

		@include small-tablet {
			width: 100%;
		}
	}

	&__bottom {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		flex: 1 1 auto;
		padding-top: 50px;
	}

	&__btn {
		margin-bottom: 15px;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}

		&:hover {
			background-color: var(--light-color);
			color: var(--blue-color);
		}
	}

	@include small-tablet {
		display: block;
		max-width: 100%;
	}
}

.form-field {
	display: flex;
	flex-direction: column;

	&__caption {
		margin-bottom: 5px;
		font-weight: 400;
		font-size: 15px;
		line-height: 18px;
		color: var(--dark-color);
	}

	&__text {
		margin-bottom: 5px;
		font-weight: 400;
		font-size: 10px;
		line-height: 12px;
		color: var(--dark-color);
	}

	&__input {
		padding: 15px;
		border-radius: 9px;
		border: 1px solid rgba(59, 59, 59, 0.31);
		resize: none;
		width: 100%;
		height: 50px;
		font-size: 15px;
		color: var(--dark-color);
		background-color: var(--light-color);

		&::placeholder {
			font-weight: 400;
			font-size: 13px;
			line-height: 15px;
			color: #B0B9C2;
		}

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}
	}

	&__input--big {
		height: 124px;
	}

	&__date {
		padding: 8px;
		border-radius: 6px;
		font-weight: 400;
		font-size: 12px;
		line-height: 14px;
		color: var(--dark-color);

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}
	}

	&__time {
		padding: 8px;
		border-radius: 6px;
		font-weight: 400;
		font-size: 12px;
		line-height: 14px;
		color: var(--dark-color);

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}
	}
}

.custom-checkbox {


	&__input {
		position: absolute;
		opacity: 0;
	}

	&__consent {
		position: relative;
		display: inline-block;
		padding-left: 32px;

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}
	}

	&__text {
		font-weight: 400;
		font-size: 8px;
		line-height: 128%;
		color: var(--dark-color);
	}

	&__consent::before {
		content: "";
		position: absolute;
		left: 0px;
		top: 3px;
		display: inline-block;
		border-radius: 5px;
		border: 2px solid var(--blue-color);
		width: 22px;
		height: 22px;
		transition: background-color 0.3s ease-in-out;
	}

	&__consent::after {
		content: "";
		position: absolute;
		left: 0px;
		top: 3px;
		width: 22px;
		height: 22px;
		background-image: url("/img/check.svg");
		background-position: center;
		background-size: 16px 16px;
		background-repeat: no-repeat;
		opacity: 0;
		transition: opacity 0.3s ease-in-out;
	}

	&__input:checked+&__consent::before {
		background-color: var(--blue-color);
	}

	&__input:checked+&__consent::after {
		opacity: 1;
	}
}