.equipment {
	padding-top: 80px;
	padding-bottom: 80px;
	background-color: var(--solitude-color);

	&__title {
		margin: 0;
		margin-bottom: 50px;
		width: 60%;

		@include small-desktop {
			width: 100%;
		}
	}

	&__box {
		display: grid;
		grid-template-columns: repeat(6, auto);
		grid-auto-rows: 190px;
		gap: 35px;

		@include small-desktop {
			gap: 20px;
		}

		@include small-tablet {
			display: block;
		}
	}

	&__image {
		grid-column: span 2;
		border-radius: 25px;
		border: 3px solid #FFFFFF;
		overflow: hidden;

		&--big {
			grid-column: span 2;
			grid-row: span 2;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&:not(:last-child) {
			@include small-tablet {
				margin-bottom: 30px;
			}
		}
	}
}