.information {
	padding-top: 80px;
	padding-bottom: 80px;
	background-color: var(--light-color);

	&__title {
		margin: 0;
		margin-bottom: 100px;
	}

	&__box {
		position: relative;
		display: flex;
		justify-content: space-between;
		padding: 29px 60px;
		border-radius: 95.9795px;
		height: 105px;
		box-shadow: 0px -1.50556px 11.2917px rgba(0, 0, 0, 0.06), -50.2154px 56.0821px 42.1557px rgba(0, 0, 0, 0.03), -32.547px 36.3495px 24.6884px rgba(0, 0, 0, 0.0227778), -19.3422px 21.602px 13.4274px rgba(0, 0, 0, 0.0182222), -10.0431px 11.2164px 6.8503px rgba(0, 0, 0, 0.015), -4.09162px 4.56965px 3.43491px rgba(0, 0, 0, 0.0117778), -0.929914px 1.03856px 1.6589px rgba(0, 0, 0, 0.00722222);

		@include small-desktop {
			padding: 30px;
		}

		@include small-tablet {
			flex-wrap: wrap;
			padding: 15px;
			border-radius: 0;
			height: 100%;
		}
	}

	&__left {
		@include small-tablet {
			padding: 10px;
		}
	}

	&__right {
		@include small-tablet {
			padding: 10px;
		}
	}

	&__link {
		display: flex;
		align-items: center;
		justify-content: center;
		max-width: 288px;
		font-weight: 500;
		font-size: 22px;
		line-height: 104%;
		color: var(--blue-color);
		transition: color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}

		&:hover {
			color: #0769d9;
		}

		@include mobile {
			font-size: 20px;
		}
	}

	&__icon {
		margin-right: 15px;
		width: 35px;
		height: 35px;
		object-fit: contain;
	}

	&__center {
		position: absolute;
		left: 50%;
		top: 0;
		padding: 35px 60px;
		border-radius: 95.9795px;
		box-shadow: 0px -1.50556px 11.2917px rgba(0, 0, 0, 0.06), -50.2154px 56.0821px 42.1557px rgba(0, 0, 0, 0.03), -32.547px 36.3495px 24.6884px rgba(0, 0, 0, 0.0227778), -19.3422px 21.602px 13.4274px rgba(0, 0, 0, 0.0182222), -10.0431px 11.2164px 6.8503px rgba(0, 0, 0, 0.015), -4.09162px 4.56965px 3.43491px rgba(0, 0, 0, 0.0117778), -0.929914px 1.03856px 1.6589px rgba(0, 0, 0, 0.00722222);
		width: 320px;
		transform: translateX(-50%);
		background-color: var(--light-color);

		@include tablet {
			top: -85px;
		}

		@include small-tablet {
			display: flex;
			justify-content: center;
			padding: 25px;
			border-radius: 0;
			width: 100%;
		}

		@include mobile-big {
			justify-content: flex-start;
		}
	}
}