.calling {
	padding: 70px 0;
	background-color: var(--blue-color);

	&__container {}

	&__inner {
		position: relative;
		display: grid;
		grid-template-columns: repeat(7, 1fr);
		column-gap: 160px;

		@include tablet {
			grid-template-columns: repeat(6, 1fr);
			column-gap: 30px;
		}
	}

	&__content {
		grid-column: 4 span;

		@include tablet {
			grid-column: span 3;
		}

		@include mobile {
			grid-column: span 6;
			margin-bottom: 40px;
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 35px;
		max-width: 400px;
		color: var(--light-color);
	}


	&__descr {
		margin: 0;
		margin-bottom: 110px;
		max-width: 380px;
		font-size: 20px;
		line-height: 115%;
		color: var(--light-color);

		@include tablet {
			margin-bottom: 40px;
		}

		@include mobile {
			font-size: 18px;
		}
	}

	&__btn {
		padding: 17px 44px;
		font-weight: 400;
		font-size: 17px;
		line-height: 20px;
		background-color: #B6E300;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--light-color);
			outline-offset: 2px;
		}

		&:hover {
			background-color: var(--light-color);
			color: var(--blue-color);
		}
	}

	&__image {
		grid-column: 3 span;
		border-radius: 27px;
		overflow: hidden;

		&::before {
			content: '';
			position: absolute;
			top: -35px;
			right: 20px;
			width: 65px;
			height: 55px;
			background-image: url('/img/benefits/quote.png');
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;

			@include mobile {
				display: none;
			}
		}

		@include mobile {
			grid-column: span 6;
		}
	}
}