.license {
	padding-top: 80px;
	padding-bottom: 45px;
	background-color: var(--light-color);

	&__title {
		margin: 0;
		margin-bottom: 40px;
	}

	&__columns {
		display: flex;
		padding: 40px 80px 45px 40px;
		border-radius: 25px;
		border: 5px solid #E5F1FF;
		height: 530px;

		@include small-desktop {
			padding: 20px;
		}

		@include tablet {
			display: block;
			height: auto;
		}

	}

	&__col {
		flex: 1 0 24.1%;

		&:not(:last-child) {
			margin-right: 80px;

			@include small-desktop {
				margin-right: 40px;
			}

			@include mobile {
				margin-right: 0;
			}
		}

		@include tablet {
			margin-right: 0;
			margin-bottom: 40px;
		}
	}

	.license__col--big {
		flex: 1 0 39.5%;
		margin-right: 50px;

		@include mobile {
			margin-right: 0;
		}
	}

	&__text {
		position: relative;
		font-style: italic;
		font-weight: 500;
		font-size: 28px;
		line-height: 117%;
		color: var(--dark-color);

		&::before {
			position: absolute;
			content: '';
			left: 24px;
			top: 48%;
			width: 40px;
			height: 3px;
			background-color: #C1F100;
			transform: translateX(-50%);

			@include tablet {
				display: none;
			}
		}

		p {
			margin: 0;
			margin-top: 30px;
			font-style: normal;
			font-size: 21px;
			line-height: 119%;
			color: var(--dark-color);
			opacity: 0.7;

			@include mobile {
				font-size: 18px;
			}
		}

		@include mobile {
			font-size: 22px;
		}
	}

	&__image {
		display: block;
		margin-bottom: 40px;
		max-width: 100%;
		height: 370px;

		@include mobile {
			height: auto;
		}
	}

	&__centered {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;

		&::before {
			position: absolute;
			content: '';
			left: 50%;
			top: -20px;
			width: 40px;
			height: 3px;
			background-color: #C1F100;
			transform: translateX(-50%);

			@include tablet {
				left: 0;
				margin-left: 125px;
			}
		}

		@include tablet {
			justify-content: flex-start;
		}
	}

	&__descr {
		font-weight: 600;
		font-size: 19px;
		line-height: 94%;
		text-align: center;
		color: var(--dark-color);
		transition: color 0.3s ease-in-out;

		span {
			font-weight: 400;
		}


		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}

		&:hover {
			color: var(--blue-color);
		}
	}
}