.portfolio {
	padding-top: 80px;
	padding-bottom: 80px;
	background-color: var(--light-color);

	&__title {
		margin: 0;
		margin-bottom: 10px;
	}

	&__descr {
		margin: 0;
		margin-bottom: 65px;
		max-width: 575px;
		font-weight: 400;
		font-size: 20px;
		line-height: 115%;
		color: var(--dark-color);

		@include mobile {
			font-size: 18px;
		}
	}

	&__list {
		margin-bottom: 110px;
	}
}

.list-portfolio {
	display: grid;
	grid-template-columns: repeat(9, 1fr);
	column-gap: 135px;

	&__item {
		grid-column: 3 span;
		display: flex;
		flex-direction: column;

		@include small-tablet {
			grid-column: span 4;
			align-items: center;

			&:not(:last-child) {
				margin-bottom: 40px;
			}
		}
	}

	&__image {
		margin-bottom: 12px;
	}

	&__name {
		margin-bottom: 12px;
		font-weight: 600;
		font-size: 21px;
		line-height: 25px;
		color: var(--dark-color);

		@include tablet {
			font-size: 18px;
		}
	}

	&__post {
		font-weight: 400;
		font-size: 12px;
		line-height: 14px;
		color: var(--dark-color);
		opacity: 0.8;

		@include mobile {
			font-size: 13px;
		}
	}

	&__career {
		margin-bottom: 3px;

		&>:not(:last-child) {
			margin-bottom: 3px;
		}
	}

	&__descr {
		position: relative;
		margin: 0;
		padding-left: 13px;
		font-weight: 400;
		font-size: 15px;
		line-height: 120%;
		color: var(--dark-color);

		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 6px;
			width: 4px;
			height: 4px;
			border-radius: 100px;
			background-color: var(--dark-color);
		}
	}

	&__value {
		margin-bottom: 25px;
		font-weight: 600;
		font-size: 18px;
		line-height: 21px;
		color: var(--dark-color);

		@include tablet {
			font-size: 16px;
		}
	}

	&__btns {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-top: auto;

		@include tablet {
			flex-wrap: wrap;
			justify-content: center;
		}
	}

	&__btn {
		margin-top: auto;
		padding: 17px 44px;
		font-weight: 400;
		font-size: 17px;
		line-height: 20px;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}

		&:hover {
			background-color: var(--light-color);
			color: var(--blue-color);
		}

		@include tablet {
			margin-bottom: 15px;
		}

		@include small-tablet {
			margin-right: 30px;
			margin-left: 30px;
		}
	}

	&__link {
		font-weight: 700;
		font-size: 17px;
		line-height: 20px;
		text-decoration: underline;
		color: var(--blue-color);
		transition: color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}

		&:hover {
			color: #F06150;
		}
	}

	@include small-desktop {
		column-gap: 60px;
	}

	@include small-tablet {
		grid-template-columns: repeat(4, 1fr);
	}
}

.experts {
	display: flex;
	align-items: center;
	justify-content: center;

	&__image {
		position: relative;
		width: 69px;
		height: 69px;

		&--expert-1 {
			z-index: 5;
		}

		&--expert-2 {
			z-index: 10;
			right: 20px;
		}

		&--expert-3 {
			z-index: 15;
			right: 40px;
		}

		@include mobile {
			margin-bottom: 30px;
		}
	}

	&__descr {
		display: flex;
		align-items: center;
		font-weight: 400;
		font-size: 20px;
		line-height: 23px;
		color: var(--dark-color);
		transition: fill 0.3s ease-in-out, color 0.3s ease-in-out;

		&:hover {
			color: #F06150;
		}

		@include mobile {
			font-size: 19px;
		}
	}

	&__icon {
		flex-shrink: 0;
		width: 55px;
		height: 30px;
		fill: var(--blue-color);
		transition: fill 0.3s ease-in-out;

		&:hover {
			fill: #F06150;
		}
	}

	@include mobile {
		flex-wrap: wrap;
	}
}