.reception {
	padding-top: 80px;
	padding-bottom: 80px;
	background-color: var(--blue-color);

	&__title {
		margin: 0;
		margin-bottom: 20px;
		width: 100%;
		color: var(--light-color);
	}

	&__descr {
		margin: 0;
		margin-bottom: 60px;
		font-weight: 400;
		font-size: 22px;
		line-height: 117%;
		color: var(--light-color);

		@include tablet {
			font-size: 20px;
		}
	}

	.form-reception {
		display: flex;
		align-items: flex-start;

		&__field {
			flex: 0 1 19.5%;

			&:not(:last-child) {
				margin-right: 30px;

				@include tablet {
					margin-bottom: 30px;
				}
			}
		}

		&__box {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			margin-left: 10px;
			padding-top: 25px;
		}

		&__btn {
			margin-bottom: 20px;
			padding: 12px 41px;
			font-size: 17px;
			background-color: #B6E300;
			transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

			&:focus {
				outline: 2px solid var(--light-color);
				outline-offset: 2px;
			}

			&:hover {
				background-color: var(--light-color);
				color: var(--blue-color);
			}

			@include tablet {
				font-size: 20px;
			}
		}

		&__checkbox {
			max-width: 330px;
		}

		@include tablet {
			display: block;
		}
	}

	.form-field__caption {
		color: var(--light-color);

		@include tablet {
			font-size: 18px;
		}
	}

	.form-field__input {
		padding: 12px;

		&::placeholder {
			@include tablet {
				font-size: 16px;
			}
		}

		&:focus {
			outline: 2px solid var(--light-color);
			outline-offset: 2px;
		}
	}

	.custom-checkbox {
		&__text {
			margin: 0;
			color: #FFFFFF;
		}

		&__consent::before {
			content: "";
			position: absolute;
			left: 0px;
			top: 3px;
			display: inline-block;
			border-radius: 5px;
			border: 2px solid var(--blue-color);
			width: 22px;
			height: 22px;
			background-color: var(--light-color);
			transition: background-color 0.3s ease-in-out;
		}

		&__consent::after {
			content: "";
			position: absolute;
			left: 0px;
			top: 3px;
			width: 22px;
			height: 22px;
			background-image: url("/img/svg/check.svg");
			background-position: center;
			background-size: 16px 16px;
			background-repeat: no-repeat;
			opacity: 0;
			transition: opacity 0.3s ease-in-out;
		}

		.custom-checkbox__input:checked+.custom-checkbox__consent::after {
			opacity: 1;
		}

		.custom-checkbox__input:checked+.custom-checkbox__consent::before {
			background-color: var(--light-color);
		}
	}
}