.comments {
	padding-top: 80px;
	padding-bottom: 80px;
	background-color: var(--light-color);

	&__title {
		margin: 0;
		margin-bottom: 80px;
		width: 80%;

		@include tablet {
			width: 100%;
		}

		@include mobile {
			margin-bottom: 40px;
		}
	}

	&__box {
		display: flex;
		margin-bottom: 110px;

		@include small-tablet {
			flex-wrap: wrap;
		}
	}

	&__left {
		position: relative;
		padding: 25px 30px;
		max-width: 425px;
		font-weight: 700;
		font-size: 28px;
		line-height: 117%;
		color: var(--light-color);

		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			z-index: 10;
			width: 100%;
			height: 100%;
			border-radius: 25px;
			border: 5px solid #2186FA;
			background: linear-gradient(180deg, #2186FA 0%, rgba(33, 134, 250, 0.7) 100%);
		}

		@include small-tablet {
			max-width: 100%;
			margin-bottom: 30px;
			padding: 20px;
		}
	}

	&__text {
		margin: 0;
		font-weight: 400;
		font-size: 27px;
		line-height: 121%;
		color: var(--dark-color);

		span {
			font-weight: 600;
		}

		&--light {
			position: relative;
			z-index: 20;
			font-weight: 700;
			font-size: 28px;
			line-height: 117%;
			color: var(--light-color);

			@include tablet {
				font-size: 24px;
			}
		}

		@include tablet {
			font-size: 24px;
		}

		@include mobile {
			font-size: 20px;
		}
	}

	&__right {
		position: relative;
		right: 20px;
		padding: 25px 50px;
		margin-right: 20px;
		border-radius: 0 25px 25px 0;
		border: 5px solid #E5F1FF;
		border-left: none;

		@include small-tablet {
			padding: 20px;
			margin-right: 0;
			right: 0;
			border: 5px solid #E5F1FF;
			border-radius: 25px;
		}
	}

	&__icon {
		position: absolute;
		top: -30px;
		right: 0;
		width: 60px;
		height: 50px;
		fill: var(--blue-color);

		@include small-tablet {
			display: none;
		}
	}

	&__list {
		display: flex;
		flex-direction: column;
		margin-bottom: 50px;
	}

	&__item {
		position: relative;
		border-radius: 127.5px;
		box-shadow: 0px -2px 15px rgba(0, 0, 0, 0.06),
			-66.7066px 74.4999px 56px rgba(0, 0, 0, 0.03),
			-43.2357px 48.287px 32.7963px rgba(0, 0, 0, 0.0227778),
			-25.6944px 28.6963px 17.837px rgba(0, 0, 0, 0.0182222),
			-13.3413px 14.9px 9.1px rgba(0, 0, 0, 0.015),
			-5.43535px 6.07036px 4.56296px rgba(0, 0, 0, 0.0117778),
			-1.23531px 1.37963px 2.2037px rgba(0, 0, 0, 0.00722222);

		&:not(:last-child) {
			margin-bottom: 60px;
		}

		@include small-tablet {
			border-radius: 0;
		}
	}
}

.comments__item {
	&:nth-child(n + 4) {
		display: none;
	}
}

.comments__item.is-visible {
	display: inline-flex;
}

.load-more {
	display: flex;
	align-items: center;
	justify-content: center;

	&__btn {
		padding: 25px 60px;
		border-radius: 40px;
		overflow-anchor: none;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}

		&:hover {
			background-color: var(--light-color);
			color: var(--blue-color);
		}

		@include mobile {
			padding: 20px 40px;
		}
	}
}

.confidentiality {
	display: flex;
	justify-content: flex-end;
	margin-bottom: 50px;

	&__descr {
		margin: 0;
		padding: 20px;
		border-radius: 13px;
		border: 5px solid #E5F1FF;
		max-width: 540px;
		font-weight: 500;
		font-size: 17px;
		line-height: 117%;
		color: var(--dark-color);
	}
}

.comment {
	padding: 15px 100px 25px 15px;
	border-radius: 127.5px;
	overflow: hidden;

	&__inner {
		display: flex;

		@include small-tablet {
			flex-wrap: wrap;
		}
	}

	&__left {
		@include mobile {
			margin-bottom: 40px;
		}
	}

	&__image {
		position: relative;
		display: block;
		max-width: 200px;

		@include small-tablet {
			max-width: 150px;
		}
	}

	&__author {
		position: absolute;
		left: 40px;
		bottom: 0;
		margin: 0;
		padding: 15px;
		border-radius: 15px;
		width: 188px;
		height: 55px;
		font-weight: 600;
		font-size: 21px;
		line-height: 106%;
		text-align: center;
		color: var(--blue-color);
		background-color: var(--light-color);

		span {
			font-weight: 400;
		}

		@include small-tablet {
			left: 180px;
			top: 110px;
			padding: 10px;
		}

		@include mobile {
			left: -3px;
			top: 170px;
			background: none;
		}
	}

	&__right {
		max-width: 810px;

		@include small-tablet {
			margin-top: 20px;
		}
	}

	&__content {
		padding: 20px;

		* {
			margin-bottom: 20px;
			font-weight: 400;
			font-size: 19px;
			line-height: 132%;
			color: var(--dark-color);
		}

		h3 {
			margin: 0;
			margin-bottom: 17px;
			font-weight: 600;
			font-size: 28px;
			line-height: 106%;
			color: var(--dark-color);

			@include tablet {
				font-size: 24px;
			}

			@include mobile {
				font-size: 20px;
			}
		}

		p {
			margin: 0;
			font-weight: 400;
			font-size: 19px;
			line-height: 132%;
			color: var(--dark-color);

			&:not(:last-child) {
				margin-bottom: 20px;
			}

			@include tablet {
				font-size: 17px;
				height: 270px;
			}

			@include mobile {
				font-size: 15px;
			}
		}

		@include tablet {
			height: 270px;
			overflow: hidden;
			overflow-y: auto;
		}

		@include mobile {
			padding: 0;
			padding-right: 10px;
		}
	}

	&__next {
		margin: 0;
		font-weight: 500;
		font-size: 19px;
		line-height: 22px;
		color: var(--blue-color);
		transition: color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--blue-color);
			outline-offset: 2px;
		}

		@include mobile {
			font-size: 16px;
		}
	}

	&__icon {
		position: relative;
		top: 6px;
		margin: 0;
		width: 40px;
		height: 22px;
		fill: var(--blue-color);
	}

	@include small-tablet {
		padding-right: 15px;
		border-radius: 0;
	}
}